if (typeof window !== 'undefined' && window.Vue) {
  window.Vue.component('VPolyLine', component.exports)
}
export default {
  name: 'VPolyLine',

  props: {
    points: {
      type: Array,
      required: true
    }
  },

  computed: {
    draw() {
      return this.points
        .reduce((result, point) => {
          return `${result} ${point.x},${point.y}`
        }, '')
        .trim()
    }
  }
}
